<template>
  <div class="main">
    <div>
      <router-view></router-view>
    </div>
    <van-tabbar class="vantab" v-model="active">
      <van-tabbar-item v-for="(item, index) in tabbar" :key="index" :to="{name: item.routerName}">
        <span>{{item.title}}</span>
        <template #icon="props">
          <img :src="props.active ? item.active : item.inactive" />
        </template>
      </van-tabbar-item>
    </van-tabbar>
  </div>
</template>

<script>


export default {
  name: 'Main',
  data() {
    return {
      active: 0,
      tabbar: [{
        title: '首页',
        routerName: 'Home',
        active: require('../assets/icon/home_active.png'),
        inactive: require('../assets/icon/home.png'),
      },{
        title: '课堂',
        routerName: 'Classroom',
        active: require('../assets/icon/classroom-active.png'),
        inactive: require('../assets/icon/classroom.png')
      },{
        title: '商城',
        routerName: 'Shop',
        active: require('../assets/icon/shop-active.png'),
        inactive: require('../assets/icon/shop.png'),
      },{
        title: '收藏夹',
        routerName: 'Collect',
        active: require('../assets/icon/collect-active.png'),
        inactive: require('../assets/icon/collect.png'),
      },{
        title: '我的',
        routerName: 'My',
        active: require('../assets/icon/my-active.png'),
        inactive: require('../assets/icon/my.png'),
      }]
    }
  },

  created() {
    let routeName = this.$route.name;
    for(let i = 0; i < this.tabbar.length; i++) {
      if (this.tabbar[i].routerName === routeName) {
        this.active = i;
      }
    }
  },
}
</script>
<style lang="less" scoped>

.vantab{
  z-index: 99;
}

/deep/.van-tabbar-item--active{
  color: #facc30;
}
</style>

